<template>
  <div
    class="container-register"
    :class="{
      english: $store.getters.adminData.language == 'english',
      arabic: $store.getters.adminData.language == 'arabic',
    }"
  >
    <div class="row">
      <div class="col-lg-6">
        <!---IMAGE--->
        <img
          src="../../src/assets/img/study.webp"
          class="img-fluid"
          alt=""
          width="600"
          height="auto"
        />
      </div>
      <div class="col-lg-6" style="background: #fbfbfb">
        <div class="form">
          <h3 class="register-heading">
            {{
              type === "update"
                ? "تعديل البيانات"
                : mode === "signin"
                ? "تسجيل الدخول"
                : "أنشاء حساب" | translate
            }}
          </h3>
          <hr />
          <p v-if="mode == 'signup'">
            {{ "الرجاء ادخال بياناتك صحيحة والتأكد منها" | translate }}
          </p>
          <p v-if="type === 'update'">تأكد من تذكر كلمة المرور الجديدة جيدا</p>
          <p v-if="mode === 'signin'">ادخل بياناتك لمباشرة نشاطك</p>
          <hr />
          <br />
          <div class="row register-form">
            <div class="col-lg-6">
              <div v-if="mode != 'signin'" class="form-group">
                <label>الإسم بالكامل</label>
                <input
                  class="form-control"
                  v-model="fullname"
                  :counter="10"
                  placeholder="الإسم بالكامل"
                  required
                  @input="$v.fullname.$touch()"
                  @blur="$v.fullname.$touch()"
                />
                <p class="error-msg" v-for="err in fullnameErrors" :key="err">
                  {{ err }}
                </p>
              </div>
              <div
                v-if="type === 'signup' || this.type == 'updateUserByAdmin'"
                class="form-group"
              >
                <label>السنة الدراسية</label>
                <select
                  id="stage"
                  class="form-control"
                  v-model="stage"
                  :counter="10"
                  required
                  @input="$v.stage.$touch()"
                  @blur="$v.stage.$touch()"
                >
                  <option selected disabled>السنة الدراسية</option>
                  <option
                    v-for="(stage, i) of $store.getters.stages"
                    :key="i"
                    :value="stage.value"
                  >
                    {{ stage.text }}
                  </option>
                </select>
                <p class="error-msg" v-for="err in stageErrors" :key="err">{{ err }}</p>
              </div>
              <div v-if="type !== 'signin'" class="form-group">
                <label>رقم التليفون</label>
                <input
                  class="form-control"
                  v-model="phone"
                  placeholder="رقم التليفون"
                  required
                  @input="$v.phone.$touch()"
                  @blur="$v.phone.$touch()"
                />
                <p class="error-msg" v-for="err in phoneErrors" :key="err">{{ err }}</p>
              </div>
              <!---------------------->
              <!---------------------->
              <!---------------------->
              <div v-if="type != 'signin'" class="form-group">
                <label>رقم تليفون ولي الأمر</label>
                <input
                  class="form-control"
                  v-model="parentPhone"
                  placeholder="رقم ولي الأمر"
                  required
                  @input="$v.parentPhone.$touch()"
                  @blur="$v.parentPhone.$touch()"
                />
                <p class="error-msg" v-for="err in parentPhoneErrors" :key="err">
                  {{ err }}
                </p>
              </div>
              <div v-if="type != 'signin'" class="form-group">
                <label>
                  السنتر وميعاد الحصة أو العنوان أو كود مميز على حسب الاتفاق مع المدرس أو
                  يمكنك تركها فارغة
                </label>
                <input
                  class="form-control"
                  v-model="address"
                  placeholder="السنتر و ميعاد الحصة"
                  required
                  @input="$v.email.$touch()"
                  @blur="$v.address.$touch()"
                />
                <p class="error-msg" v-for="err in addressErrors" :key="err">{{ err }}</p>
              </div>
              <!----------------------->
              <!----------------------->
              <!----------------------->
            </div>
            <div :class="mode === 'signin' ? 'col-lg-12' : 'col-lg-6'">
              <div class="form-group">
                <label>البريد الإلكتروني أو أسم مميز</label>
                <input
                  class="form-control"
                  type="email"
                  v-model="email"
                  placeholder="البريد الإلكتروني"
                  required
                  @input="$v.email.$touch()"
                  @blur="$v.email.$touch()"
                />
                <p class="error-msg" v-for="err in emailErrors" :key="err">{{ err }}</p>
              </div>
              <div v-if="type === 'update' && changePassword" class="form-group">
                <label>كلمة المرور القديمة</label>
                <input
                  type="password"
                  class="form-control"
                  v-model="oldPassword"
                  placeholder="كلمة المرور القديمة"
                  required
                  @input="$v.oldPassword.$touch()"
                  @blur="$v.oldPassword.$touch()"
                />
                <p class="error-msg" v-for="err in oldPasswordErrors" :key="err">
                  {{ err }}
                </p>
              </div>
              <div
                v-if="
                  type !== 'updateUserByAdmin' ||
                  (type == 'updateUserByAdmin' && changePassword)
                "
                class="form-group"
              >
                <label>كلمة المرور</label>
                <input
                  type="password"
                  class="form-control"
                  v-model="password"
                  placeholder="كلمة المرور"
                  required
                  @input="$v.password.$touch()"
                  @blur="$v.password.$touch()"
                />
                <p class="error-msg" v-for="err in passwordErrors" :key="err">
                  {{ err }}
                </p>
              </div>
              <div v-if="type == 'signup' || changePassword" class="form-group">
                <label>تأكيد كلمة المرور</label>
                <input
                  type="password"
                  class="form-control"
                  v-model="confirm"
                  placeholder="تأكيد كلمة المرور"
                  required
                  @input="$v.confirm.$touch()"
                  @blur="$v.confirm.$touch()"
                />
                <p class="error-msg" v-for="err in confirmErrors" :key="err">{{ err }}</p>
              </div>
              <app-loading v-if="authLoading || loading" />
            </div>
            <div v-if="type == 'signin'" class="container d-flex">
              <a @click="resetPassword" class="text-center">استعادة كلمة المرور</a>
            </div>
            <div class="col-12">
              <button
                v-if="type === 'update' || type == 'updateUserByAdmin'"
                @click="switchChangePassword"
                class="btn btn-link btn-block"
              >
                {{ changePassword ? "عدم تغيير كلمة المرور" : "تغيير كلمة المرور" }}
              </button>
              <button
                :disabled="
                  ((type == 'update' || type == 'updateUserByAdmin') && $v.$anyError) ||
                  (type == 'signup' && $v.$anyError) ||
                  (type == 'signin' && ($v.password.$anyError || $v.email.$anyError))
                "
                class="btn btn-primary btn-block"
                style="text-center"
                @click.prevent="
                  () => {
                    type === 'update' || type == 'updateUserByAdmin'
                      ? update()
                      : mode === 'signin'
                      ? signin()
                      : signup();
                  }
                "
              >
                {{
                  type === "update" || type == "updateUserByAdmin"
                    ? "تعديل"
                    : mode === "signin"
                    ? "تسجيل الدخول"
                    : "انشاء حساب"
                }}
              </button>

              <button
                v-if="type != 'update' && type != 'updateUserByAdmin'"
                class="btn btn-link btn-block"
                @click="switchMode"
              >
                {{
                  mode === "signin"
                    ? "ليس لديك حساب اضغط هنا لانشاء حساب جديد"
                    : "لديك حساب اضغط هنا لتسجيل الدخول"
                }}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {
  email,
  required,
  minLength,
  sameAs,
  requiredUnless,
} from "vuelidate/lib/validators";
import loading from "../components/loading.vue";
export default {
  created() {
    const route = this.$route.path.split("/")[1];
    if (route == "register") {
      this.mode = this.$route.params.mode;
      this.pageToGo = this.$route.params.pageToGo;
    } else if (route == "update") {
      this.mode = "signup";
      this.userId = this.$route.params.id
        ? this.$route.params.id
        : this.$store.getters.userId;
      axios.post("/getuserdata", { _id: this.userId }).then((res) => {
        this.email = res.data.userdata.email;
        this.fullname = res.data.userdata.fullname;
        this.phone = res.data.userdata.phone;
        this.parentPhone = res.data.userdata.parentPhone;
        this.address = res.data.userdata.address;
        this.stage = res.data.userdata.stage;
      });
    }

    if (route == "register" && this.mode == "signin") this.type = "signin";
    else if (route == "register" && this.mode == "signup") this.type = "signup";
    else if (
      route == "update" &&
      this.token &&
      this.mode == "signup" &&
      !this.$route.params.id
    )
      this.type = "update";
    else if (
      route == "update" &&
      this.token &&
      this.role == 1 &&
      this.mode == "signup" &&
      this.$route.params.id
    )
      this.type = "updateUserByAdmin";
  },
  components: {
    appLoading: loading,
  },
  data() {
    return {
      email: "",
      oldPassword: "",
      password: "",
      confirm: "",
      fullname: "",
      phone: "",
      parentPhone: "",
      address: "",
      mode: "signin",
      stage: "6",
      type: "signin",
      pageToGo: "home",
      loading: false,
      changePassword: false,
      userId: null,
      thisIsAdmin: false,
    };
  },
  validations: {
    email: {
      required,
      // email,
      minLen: minLength(6),
    },
    oldPassword: {
      required,
      minLen: minLength(6),
    },
    password: {
      required,
      minLen: minLength(6),
    },
    confirm: {
      required, //requiredUnless(() => this.mode === 'signup'),
      sameAs: sameAs((vm) => vm.password),
    },
    fullname: {
      required,
      minLen: minLength(6),
    },
    stage: {
      required,
    },
    phone: {
      required,
      minLen: minLength(11),
    },
    parentPhone: {
      required,
      minLen: minLength(11),
    },
    address: {
      // required,
      // minLen: minLength(9),
    },
  },
  watch: {
    mode(val) {
      if (this.$route.path.split("/")[1] == "register" && val == "signin")
        this.type = "signin";
      if (this.$route.path.split("/")[1] == "register" && val == "signup")
        this.type = "signup";
    },
  },
  computed: {
    role() {
      return localStorage.getItem("role");
    },
    token() {
      return localStorage.getItem("token");
    },
    authLoading() {
      return this.$store.getters.authLoading;
    },
    // userId() {
    //   return this.$store.getters.userId;
    // },
    fullnameErrors() {
      const errors = [];
      if (!this.$v.fullname.$dirty) return errors;
      !this.$v.fullname.required && errors.push("الاسم بالكامل مطلوب");
      !this.$v.fullname.minLen && errors.push("الاسم قصير");
      return errors;
    },
    stageErrors() {
      const errors = [];
      if (!this.$v.fullname.$dirty) return errors;
      !this.$v.fullname.required && errors.push("المرحلة الدراسية مطلوبة");
      return errors;
    },
    phoneErrors() {
      const errors = [];
      if (!this.$v.phone.$dirty) return errors;
      !this.$v.phone.required && errors.push("رقم التليفون مطلوب");
      !this.$v.phone.minLen && errors.push("رقم التليفون غير صحيح");
      return errors;
    },
    parentPhoneErrors() {
      const errors = [];
      if (!this.$v.parentPhone.$dirty) return errors;
      !this.$v.parentPhone.required && errors.push("رقم تليفون ولي الأمر مطلوب");
      !this.$v.parentPhone.minLen && errors.push("رقم التليفون غير صحيح");
      return errors;
    },
    addressErrors() {
      const errors = [];
      if (!this.$v.address.$dirty) return errors;
      // !this.$v.address.required && errors.push("العنوان مطلوب");
      // !this.$v.address.minLen && errors.push("العنوان قصير");
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.required && errors.push("البريد الإلكتروني مطلوب");
      // !this.$v.email.email && errors.push("البريد الإلكتروني غير صالح");
      !this.$v.email.minLen && errors.push("الأسم قصير");
      return errors;
    },
    oldPasswordErrors() {
      const errors = [];
      if (!this.$v.oldPassword.$dirty) return errors;
      !this.$v.oldPassword.required && errors.push("كلمة المرور القديمة مطلوبة");
      !this.$v.oldPassword.minLen && errors.push("كلمة المرور قصيرة");
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.required && errors.push("كلمة المرور مطلوبة");
      !this.$v.password.minLen && errors.push("كلمة المرور قصيرة");
      return errors;
    },
    confirmErrors() {
      const errors = [];
      if (!this.$v.confirm.$dirty) return errors;
      !this.$v.confirm.required && errors.push("تأكيد كلمة المرور مطلوب");
      !this.$v.confirm.sameAs && errors.push("كلمة المرور ليست متطابقة");
      return errors;
    },
  },
  methods: {
    signin() {
      this.$v.email.$touch();
      this.$v.password.$touch();
      if (this.$v.email.$anyError || this.$v.password.$anyError) return;
      this.$store.dispatch("signin", {
        email: this.email,
        password: this.password,
        pageToGo: this.pageToGo,
      });
    },

    switchMode() {
      this.mode === "signin" ? (this.mode = "signup") : (this.mode = "signin");
    },

    switchChangePassword() {
      this.changePassword = !this.changePassword;
    },

    resetPassword() {
      const confirmed = confirm(
        "هل وضعت الايمل الصحيح؟... وهل متاكد من تغيير الباسوورد؟ راجع ايميلك بعد التغيير لمعرفة كلمة مرورك الجديدة"
      );
      if (!confirmed) return;
      this.loading = true;
      axios.get("/resetPassword/" + this.email).then((res) => {
        this.loading = false;
        if (res.data.done) {
          this.$store.dispatch(
            "writemessage",
            "تم ارسال كلمة المرور الجديدة الي الايميل الخاص بيك"
          );
        } else {
          this.$store.dispatch(
            "writemessage",
            "حدثت مشكلة تأكد من الايميل واعد المحاولة أو استبدل الايميل عن طريق المدرس اذا كان غير صحيح"
          );
        }
      });
    },

    signup() {
      this.$v.email.$touch();
      this.$v.fullname.$touch();
      this.$v.password.$touch();
      this.$v.stage.$touch();
      this.$v.phone.$touch();
      this.$v.parentPhone.$touch();
      this.$v.address.$touch();
      this.$v.confirm.$touch();
      if (
        this.$v.email.$anyError ||
        this.$v.password.$anyError ||
        this.$v.fullname.$anyError ||
        this.$v.address.$anyError ||
        this.$v.phone.$anyError ||
        this.$v.parentPhone.$anyError ||
        this.$v.password.$anyError ||
        this.$v.confirm.$anyError
      )
        return;

      this.$store.dispatch("signup", {
        email: this.email,
        password: this.password,
        fullname: this.fullname,
        stage: this.stage,
        phone: this.phone,
        parentPhone: this.parentPhone,
        address: this.address,
      });
    },

    update() {
      this.$v.email.$touch();
      this.$v.password.$touch();
      this.$v.fullname.$touch();
      this.$v.address.$touch();
      this.$v.phone.$touch();
      this.$v.parentPhone.$touch();
      if (
        this.$v.email.$anyError ||
        this.$v.fullname.$anyError ||
        this.$v.address.$anyError ||
        this.$v.phone.$anyError ||
        this.$v.parentPhone.$anyError ||
        (this.$v.password.$anyError && (this.role == 0 || this.thisIsAdmin))
      ) {
        return;
      }
      if (this.changePassword && this.type != "updateUserByAdmin") {
        this.$v.confirm.$touch();
        this.$v.oldPassword.$touch();
        if (this.$v.confirm.$anyError || this.$v.oldPassword.$anyError) return;
      } else if (this.changePassword && this.type == "updateUserByAdmin") {
        this.$v.confirm.$touch();
        if (this.$v.confirm.$anyError) return;
      }
      const data = {
        email: this.email,
        fullname: this.fullname,
        phone: this.phone,
        parentPhone: this.parentPhone,
        address: this.address,
        stage: this.stage,
      };

      if (this.type == "update" && !this.changePassword)
        data["oldPassword"] = this.password;
      else if (this.changePassword && this.type == "update") {
        data["password"] = this.password;
        data["oldPassword"] = this.oldPassword;
      } else if (this.changePassword && this.type == "updateUserByAdmin") {
        data["password"] = this.password;
      }

      let path = "/updateUserDataAdmin";
      if (this.type == "update") path = "/updateUserData";
      this.loading = true;

      axios
        .post(path, {
          id: this.userId,
          data,
        })
        .then((res) => {
          this.loading = false;
          if (res.data.err) {
            return this.$store.dispatch("writemessage", res.data.err);
          }
          this.$store.dispatch("writemessage", "تم تحديث بيانات التلميذ بنجاح");
          if (this.role == 0) {
            this.$router.replace("/");
          } else if (this.role == 1) {
            this.$router.back();
          }
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.register-heading {
  h3 {
    font-size: 2rem;
    @media (max-width: 1000px) {
      font-size: 1rem;
    }
  }
}
.container-register {
  padding: 120px;
  background: rgb(255, 255, 255);
  @media (max-width: 1000px) {
    padding: 60px 22px;
  }
}
.form-group {
  .error-msg {
    color: #dc3545;
    margin-top: 5px;
    &::before {
      content: "\f06a";
      font: normal normal normal 14px/1 FontAwesome;
      font-size: 14px;
      font-size: inherit;
      text-rendering: auto;
      -moz-osx-font-smoothing: grayscale;
      margin-left: 5px;
    }
  }
}
</style>
